import React from "react";
import styled from "styled-components";
import { Author } from "./Author";
import { IFeedBox } from "./FeedBox";

const Container = styled.div`
    position: relative;
    cursor: pointer;
    width: calc(100% / 4);
`;

const BackgroundImage = styled.img`
    width: 100%;
    height: auto;
    border-radius: 8px;
`;

export const FeedBox: React.FunctionComponent<IFeedBox.IProps> = props => {
    return <Container onClick={() => window.open(props.content.link, props.title, 'top=10, left=10, width=500, height=600, status=no, menubar=no, toolbar=no, resizable=no')}>
        <BackgroundImage src={props.content.thumbnailUrl} alt={props.title}/>
        <Author
            vendorType={props.vendorType}
            name={props.author.name}
            postedAt={props.postedAt}
            profileImgUrl={props.author.profileImgUrl}
        />
    </Container>
};
